<template>
  <section class="index_page__content_section">
    <div class="container index_page__content_container">
      <div class="gradient" v-for="(item,index) in cards.items">
        <span class="gradient__title">{{index+1 | formatIndex}} {{item.title}}</span>
        <a class="gradient__download_button">Get .PNG</a>
        <div class="gradient__background" :style="item.style"></div>
        <div class="gradient__colors_box" v-if="item.sc">
          <span class="gradient__color">{{item.sc}}</span>
          <span class="gradient__arrow_symbol">→</span>
          <span class="gradient__color">{{item.ec}}</span>
        </div>
        <div class="gradient__advanced_text" v-if="!item.sc">Many colors</div>
        <button class="gradient__copy_button" type="button">Copy CSS</button>
      </div>
    </div>
  </section>
</template>
<script>
  import api from '../api/card';

  export default {
    data() {
      return {
        cards: {
          items: [],
          count: 0
        },
        filter: {
          page: 0,
          size: 20
        }
      }
    },

    methods: {
      init() {
        this.getList();
      },
      getList() {
        api.getList(this.filter).then((data) => {
          this.cards.items = data.items;
          this.cards.count = data.count;
        }).catch((err) => {
          // error callback
        });
      }
    },
    created() {
      this.init()
    }
  }

</script>
<style lang="stylus">
  .index_page__content_section
    position relative
    z-index 1
    padding 50px 0 70px
    background-color #f6f6f6

  .gradient
    position relative
    float: left
    width calc(33.3333% - 20px)
    height 420px
    background-color #fff
    margin-right: 30px
    margin-bottom 50px
    border-radius 16px
    overflow hidden
    box-shadow 0 6px 15px rgba(36, 37, 38, 0.08)
    transition box-shadow 0.25s ease
    &:hover
      box-shadow 5px 12px 20px rgba(36, 37, 38, 0.13)
  .gradient__title
    position absolute
    left 36px
    top 29px
    font-size .8125em
    color #333435
    letter-spacing 0.03em

  .gradient__download_button
    position absolute
    z-index 1
    top 28px
    right 26px
    padding-right 30px
    text-indent -5px
    font-size 13px
    color transparent
    text-decoration none
    transtion color .2s ease, text-indent .2s ease
    &:before
      content ''
      position absolute
      right 0
      bottom 0
      top 0
      margin 0
      width: 20px
      height 20px
      border-radius 50%
      border 1px solid #383839
      background url('data:image/svg+xml;base64,') no-repeat center / 8px auto
      transition all .2s ease
    &:hover
      color: #383839
      text-indent 0
      &:before
        border-color transparent
        background-color #fff
        box-shadow 2px 3.5px 5px rgba(51, 52, 53, 0.16)

  .gradient__background
    position absolute
    left 0
    right 0
    bottom 0
    top 0
    margin auto
    border-radius 50%
    width 200px
    height 200px
    background-repeat no-repeat
    background-size cover
    background-position center
    cursor url('data:image/svg+xml;base64,') , pointer

  .gradient__colors_box
    position absolute
    left 35px
    bottom 34px

  .gradient__advanced_text
    position absolute
    left: 35px
    bottom: 34px
    font-size .8125em
    color: #333435

  .gradient__color
    font-size .8125em
    color #333435

  .gradient__arrow_symbol
    margin 0 8px 0 10px
    font-weight 700

  .gradient__copy_button
    position absolute
    right 30px
    bottom 31px
    font-size .8125em
    color #333435
    letter-spacing 0.03em
    cursor pointer
    &:before
    &:after
      content ''
      position absolute
      left 0
      bottom -2px
      height 1px
      width 13%
      background-color #333435
      transition: opacity 0.25s ease, transform 0.25s ease, width 0.3s cubic-bezier(0.68, 0.05, 0.46, 1.02), left 0.2s 0.2s ease-out
    &:after
      z-index 10
      width 30%
      background-color #fff
      opacity 0
    &:hover
      &:before
        width 100%
      &:after
        opacity 1
        left 100%
  @media (min-width: 1025px)
    .gradient:nth-of-type(3n)
      margin-right: 0
</style>




